<template>
    <div ref="rightPanel" :class="{show:show}" class="rightPanel-container">
        <div class="rightPanel-background" />
        <div class="rightPanel" :style="{'background-color':rightBg}">
            <div class="handle-button" :style="{'top':buttonTop+'px','background-color':theme}" title="right panel" @click="show=!show">
                <i :class="show?'el-icon-arrow-right':'el-icon-arrow-left'" />
            </div>
            <div class="rightPanel-items">
                <right-panel-content v-if="!isMenuCharts"/>
                <right-panel-charts v-if="isMenuCharts"/>
            </div>
        </div>
    </div>
</template>

<script>
    import { addClass, removeClass } from '@/utils/document'
    import RightPanelContent from './RightPanelContent'
    import RightPanelCharts from './RightPanelCharts'
    import { getKey } from '@/utils/collection'

    export default {
        name: 'RightPanel',
        components: { RightPanelContent, RightPanelCharts },
        props: {
            clickNotClose: {
                default: false,
                type: Boolean
            },
            buttonTop: {
                default: 250,
                type: Number
            }
        },
        data() {
            return {
                show: false,
                isMenuCharts: false,
            }
        },
        computed: {
            theme() {
                return this.$store.state.theme.customTheme
            },
            rightBg() {
                return this.$store.state.theme.rightBg
            }
        },
        watch: {
            show(value) {
                if (value && !this.clickNotClose) {
                    this.addEventClick()
                }
                if (value) {
                    addClass(document.body, 'showRightPanel')
                } else {
                    removeClass(document.body, 'showRightPanel')
                }
            },
            $route() {
                this.isMenuCharts = this.checkIsMenuCharts()
            }
        },
        mounted() {
            this.insertToBody()
        },
        beforeDestroy() {
            const elx = this.$refs.rightPanel
            elx.remove()
        },
        methods: {
            addEventClick() {
                window.addEventListener('click', this.closeSidebar)
            },
            closeSidebar(evt) {
                const parent = evt.target.closest('.rightPanel')
                if (!parent) {
                    this.show = false
                    window.removeEventListener('click', this.closeSidebar)
                }
            },
            insertToBody() {
                const newChild = this.$refs.rightPanel
                const body = document.querySelector('body')
                body.insertBefore(newChild, body.firstChild)
            },
            checkIsMenuCharts() {
                const basePath = this.$route.path.split('/').slice(1, 2).toString();
                return basePath === 'charts'
            }
        },
        created () {
            this.isMenuCharts = this.checkIsMenuCharts()

        }
    }
</script>

<style>
    .showRightPanel {
        overflow: hidden;
        position: relative;
        width: calc(100% - 15px);
    }
</style>

<style lang="scss" scoped>
    .rightPanel-background {
        position: fixed;
        top: 0;
        left: 0;
        opacity: 0;
        transition: opacity .3s cubic-bezier(.7, .3, .1, 1);
        background: rgba(0, 0, 0, .2);
        z-index: -1;
    }

    .rightPanel {
        width: 100%;
        max-width: 435px;
        height: 100vh;
        position: fixed;
        top: 0;
        right: 0;
        box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, .05);
        transition: all .25s cubic-bezier(.7, .3, .1, 1);
        transform: translate(100%);
        background: #fff;
        z-index: 40000;
    }

    .show {
        transition: all .3s cubic-bezier(.7, .3, .1, 1);

        .rightPanel-background {
            z-index: 20000;
            opacity: 1;
            width: 100%;
            height: 100%;
        }

        .rightPanel {
            transform: translate(0);
        }
    }

    .handle-button {
        width: 24px;
        height: 48px;
        position: absolute;
        left: -24px;
        text-align: center;
        font-size: 24px;
        border-radius: 6px 0 0 6px !important;
        z-index: 0;
        pointer-events: auto;
        cursor: pointer;
        color: #fff;
        line-height: 48px;
        i {
            font-size: 24px;
            line-height: 48px;
        }
    }
</style>



